<template>
  <view class="apply-container overflow-auto">
    <view class="form-container mb-10">
      <uv-form
        labelPosition="left"
        :model="form"
        :rules="rules"
        labelWidth="100"
        errorType="toast"
        ref="formRef"
      >
        <uv-form-item prop="text" borderBottom ref="item1" class="border-none">
          <view class="publish-class">
            <uv-textarea
              v-model="form.text"
              border="none"
              autoHeight
              class="textarea"
              placeholder="分享你的活动"
              maxlength="100"
              count
            />
          </view>
        </uv-form-item>
        <uv-form-item prop="imags" borderBottom ref="item1" class="border-none upload">
          <Imgs
            :previewFullImage="true"
            @getFileList="getFileList"
            :maxCount="9"
            :fileList="form.fileList"
          />
        </uv-form-item>
        <uv-form-item
          label="志愿服务角色"
          prop="role_type"
          :borderBottom="true"
          required
          ref="item1"
        >
          <uv-radio-group v-model="form.role_type" @change="roleRadioChange">
            <uv-radio label="组织者" name="1"></uv-radio>
            <uv-radio label="参与者" name="2"></uv-radio>
          </uv-radio-group>
        </uv-form-item>
        <uv-form-item
          label="活动项目"
          prop="sport_tag_ids"
          borderBottom
          class="readonly"
          ref="item1"
          required
        >
          <view class="readonly-input" @click="handleSelectProject">
            <uv-input
              v-model="form.sport_tag_str"
              readonly
              disabledColor="#ffffff"
              placeholder="请选择指导项目"
              border="none"
            />
          </view>
          <template #right>
            <uv-icon name="arrow-right" color="#969799"></uv-icon>
          </template>
        </uv-form-item>
        <uv-form-item
          label="活动地址"
          prop="lat"
          borderBottom
          class="readonly"
          ref="item1"
          required
        >
          <view class="readonly-input" @click="handleClickSelect(2)">
            <uv-input
              v-model="form.realAddress"
              readonly
              disabledColor="#ffffff"
              placeholder="请选择活动地址"
              border="none"
            />
          </view>
          <template #right>
            <uv-icon name="arrow-right" color="#969799"></uv-icon>
          </template>
        </uv-form-item>
        <uv-form-item label="详细地址" prop="address" borderBottom ref="item1">
          <uv-input v-model="form.address" border="none" placeholder="请输入详细地址" />
        </uv-form-item>
        <uv-form-item label="活动时长(小时)" prop="activity_duration" borderBottom ref="item1">
          <uv-input
            v-model="form.activity_duration"
            type="number"
            border="none"
            placeholder="请输入时长"
          />
        </uv-form-item>
        <uv-form-item label="参与人数" prop="join_num" borderBottom ref="item1">
          <uv-input
            v-model="form.join_num"
            border="none"
            type="number"
            placeholder="请输入参与人数"
          />
        </uv-form-item>
        <uv-form-item
          label="活动站点"
          prop="instructor_site_id"
          borderBottom
          class="readonly"
          ref="item1"
        >
          <view class="readonly-input" @click="handleSelectSite">
            <uv-input
              v-model="form.instructor_site_id_str"
              readonly
              disabledColor="#ffffff"
              placeholder="请选择活动站点"
              border="none"
            />
          </view>
          <template #right>
            <uv-icon name="arrow-right" color="#969799" @click="handleSelectSite"></uv-icon>
          </template>
        </uv-form-item>

        <uv-form-item
          label="场地收费情况"
          prop="is_free"
          :borderBottom="false"
          ref="item1"
          required
        >
          <uv-radio-group v-model="form.is_free" @change="radioChange">
            <uv-radio label="免费" name="1"></uv-radio>
            <uv-radio label="收费" name="2"></uv-radio>
          </uv-radio-group>
        </uv-form-item>
        <view v-show="form.is_free == 2" style="margin-top: 16rpx">
          <uv-textarea
            v-model="form.is_free_note"
            placeholder="填写场地收费情况（人均X元 或 门票5元/人）"
          ></uv-textarea>
        </view>
      </uv-form>
      <!-- 选择 -->
      <uv-picker
        :defaultIndex="defaultIndex"
        :show="showPicker"
        :columns="columns"
        keyName="label"
        @confirm="handlePickerConfirm"
        @cancel="showPicker = false"
        @change="changeHandler"
      ></uv-picker>
    </view>
    <view class="footer-fixed">
      <uv-button
        type="primary"
        text="提交"
        shape="circle"
        size="medium"
        @click="submit"
      ></uv-button>
    </view>
    <canvas canvas-id="canvas" id="canvas" style="visibility: hidden"></canvas>
  </view>
</template>
<script>
import { rules } from './index'
import trainApply from '@/static/images/train-apply-bg.png'
import Imgs from '@/components/upload/imgs.vue'
import uniStorage from '@/utils/storages/uniStorage'
import { formatTreeOption, flattenTree, getPickBackFile } from '@/utils/tree'
import { getInstructorSiteList } from '@/api/instructorSite/index'
import { isMp } from '@/utils/platform'
export default {
  components: {
    Imgs,
  },
  data() {
    return {
      showPicker: false,
      trainApply,
      rules,
      form: {
        text: '',
        name: '',
        phone: '',
        select: '',
        time: '',
        addr: '',
        latng: '',
        fileList: [],
        is_free: '',
        is_free_note: '',
        instructor_site_id: '',
        instructor_site_id_str: '',
        role_type: '1',
      },
      columns: [],
      tagGroupList: [],
      pickSource: {},
      defaultIndex: [], //默认选中
      selectAddressOptions: {},
    }
  },
  methods: {
    radioChange(e) {
      this.form.is_free = e
    },
    roleRadioChange(e) {
      this.form.role_type = e
    },
    handleSelectSite() {
      const tagObj = {
        tagIds: this.form.instructor_site_id || '',
      }
      uni.navigateTo({
        url: '/pages-sub/selectActivitySite/index',
        success: (res) => {
          res.eventChannel.emit('PUBLISH_SELECT_SITE', tagObj)
        },
      })
    },
    handleSelectProject() {
      const tagObj = {
        tagIds: this.form.tagIds || this.form.tag_ids || '',
      }
      uni.navigateTo({
        url: '/pages-sub/selectActivetyProject/index',
        success: (res) => {
          res.eventChannel.emit('TAGOBJ', tagObj)
        },
      })
    },
    handleSelectProject_old() {
      this.showPicker = true
      if (this.form.sport_tag_ids) {
        let result = getPickBackFile(this.form.tagIds, this.pickSource.tagGroup, 2)
        this.defaultIndex = result.defaultIndex
        this.columns = result.columns
        return
      }
      tagData[1] = this.getDefaultColumns(flattenTree(this.pickSource.tagGroup), 1)
      this.columns = [flattenTree(this.pickSource.tagGroup)[0], tagData[1]]
      this.defaultIndex = [0, 0]
    },
    getFileList(fileList) {
      this.form.fileList = fileList
    },
    // 根据父节点过滤数据
    getDefaultColumns(data = [], rowIndex, columnIndex = 0) {
      return data[rowIndex].filter((item) => {
        return item.parentId == data[rowIndex - 1][columnIndex].id
      })
    },
    dealPickChangeReturn(columnData, level = 1, e) {
      let filterData1 = []
      let filterData2 = []
      if (level != 1) {
        filterData1 = this.getDefaultColumns(columnData, 1, e.indexs[0])
        if (filterData1.length == 0) {
          filterData1.push({
            id: '-',
            label: '无',
            parentId: columnData[0][e.indexs[0]].id,
          })
        }
        e.picker.setColumnValues(1, filterData1)
      }
      if (level == 3) {
        filterData2 = this.getDefaultColumns(columnData, 2, e.indexs[1])
        if (filterData2.length == 0) {
          filterData2.push({
            id: '-',
            label: '无',
            parentId: columnData[1][e.indexs[1]].id || '-',
          })
        }
        e.picker.setColumnValues(2, filterData2)
      }
    },
    changeHandler(e) {
      this.dealPickChangeReturn(this.tagGroupList, 2, e)
    },
    handlePickerConfirm(e) {
      this.showPicker = false
      let tag_ids = []
      let pickObj = e
      let labelString = pickObj.value
        .map((item) => {
          tag_ids.push(item.id)
          return item.label
        })
        .toString()
      if (pickObj.value[pickObj.value.length - 1].id != '-') {
        this.form.sport_tag_ids = pickObj.value[pickObj.value.length - 1].id
        this.form.sport_tag_str = pickObj.value[pickObj.value.length - 1].label
        this.form.tagIds = tag_ids
      } else {
        this.form.sport_tag_ids = ''
        this.form.sport_tag_str = ''
      }
    },
    async getDataList() {
      let res = await this.$api.getTagGroupList({
        page: 1,
        size: 1000,
        type: 'social_sports_project_kind',
        is_show_tag_id: 1,
      })
      this.pickSource.tagGroup = formatTreeOption(res.data.list, [
        'name',
        'tag_group_id',
        'tag_id_str',
        'tag_id',
        'tag_id_arr',
      ])
      this.tagGroupList = flattenTree(this.pickSource.tagGroup)
    },
    async submit() {
      this.$refs.formRef.validate().then(async () => {
        let params = JSON.parse(JSON.stringify(this.form))
        let result = []
        Object.keys(this.rules).forEach((key) => {
          if (!params[key]) {
            result.push(this.rules[key])
          }
        })
        if (params.is_free == 2 && !params.is_free_note) {
          result.push({ message: '请输入场地收费情况说明' })
        }
        if (result.length > 0) {
          this.$toast(result[0].message)
          return
        }
        if (!params.address) {
          params.address = params.realAddress
        }
        let images = params.fileList.map((item) => {
          return {
            url: item.relativeUrl,
            name: item.name,
            full_url: item.url,
          }
        })
        params.images = JSON.stringify(images)
        delete params.fileList
        this.$loading(true)
        let res = await this.$api.addVoluntaryActivityItem(params)
        this.$loading(false)
        if (res.code == 200) {
          this.$toast('发布成功', { type: 'success' })
          uni.redirectTo({ url: '/pages-sub/applyResult/success?type=activity&title=发布成功' })
        } else {
          this.$toast(res.message, { type: 'warning' })
        }
      })
    },
    handleClickSelect(type) {
      console.log(isMp)
      if (!isMp) {
        const key = 'ANEBZ-TGY63-BXY3I-OAKSC-ONSHO-NXBS6'
        const url = window.location.href.split('?')[0]
        uniStorage.set('RELEASEFORM', JSON.stringify(this.form))
        window.location.href = `https://apis.map.qq.com/tools/locpicker?search=1&type=0&backurl=${encodeURIComponent(
          url,
        )}&key=${key}&referer=社体指导员移动端`
        return
      }
      let _this = this
      uni.chooseLocation({
        success: (res) => {
          console.log(res)
          _this.form.lat = res.latitude
          _this.form.lng = res.longitude
          _this.form.addr = res.address
          _this.form.realAddress = res.address + res.name
        },
        fail(err) {
          console.log(err)
          _this.$toast('选择地址失败：' + err.errMsg)
        },
      })
    },
  },
  onLoad(options) {
    this.getDataList()
    this.selectAddressOptions = options
    uni.$on('TAGOBJ', (data) => {
      this.form = {
        ...this.form,
        sport_tag_str: data.tagIdsStr,
        sport_tag_ids: data.tag_ids,
        tagIds: data.tag_ids,
      }
    })
    uni.$on('PUBLISH_SELECT_SITE', (data) => {
      this.form.instructor_site_id = data.tagIds
      this.form.instructor_site_id_str = data.tagIdsStr
    })
  },
  watch: {
    $Route: function () {
      uniStorage.remove('RELEASEFORM')
    },
  },
  onShow() {
    const { addr, name, latng, city } = this.selectAddressOptions
    if (latng) {
      let releaseForm = uniStorage.get('RELEASEFORM')
        ? JSON.parse(uniStorage.get('RELEASEFORM'))
        : ''
      let lat = latng.split(',')[0]
      let lng = latng.split(',')[1]
      const obj = {
        addr,
        name,
        lat,
        lng,
        activity_duration: releaseForm.activity_duration,
        join_num: releaseForm.join_num,
        address: releaseForm.address,
        sport_tag_ids: releaseForm.sport_tag_ids,
        tagIds: releaseForm.tagIds,
        sport_tag_str: releaseForm.sport_tag_str,
        text: releaseForm.text,
        fileList: releaseForm.fileList,
        is_free: releaseForm.is_free,
        is_free_note: releaseForm.is_free_note,
        role_type: releaseForm.role_type,
      }
      this.form = { ...obj, realAddress: addr + city + name }
    }
  },
}
</script>

<style lang="scss" scoped>
