  padding: 32rpx;
  min-height: 100%;
  background-color: #fff;
  :deep(.form-container .uv-form-item .uni-input-input) {
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    width: auto;
  }
  :deep(.uni-textarea-wrapper) {
    min-height: 150rpx;
  }
  .submit-btn {
    margin-top: 80rpx;
    height: 80rpx;
    font-size: 28rpx;
    color: #fff;
    border-radius: 200rpx;
    line-height: 80rpx;
  }
}
:deep(.form-container .uv-form-item .uv-radio-group) {
  justify-content: flex-end;
}
:deep(.uv-radio) {
  padding-left: 15px;
}
